import React from 'react'
import * as styles from './priceProduct.module.css'

const PriceProduct = (props) => {
  return (
    <div className={styles.priceProduct}>
      <span>{props.price.name}</span>
      <span>{props.price.price} &#8381;</span>
      <span>{props.price.desc}</span>
      <ul>
        {props.price.features.map((el, index) => {
          return (
            <li key={el + `${index}`}>
              <p>{props.price.featuresKeys[index]}</p>
              <p>{el}</p>
            </li>
          )
        })}
      </ul>
      <button onClick={props.onClick}>Заказать</button>
    </div>
  )
}

export default PriceProduct
