import React from 'react'
import * as styles from './header.module.css'

const Header = () => {
  return (
    <section>
      <div className={styles.header}>
        <h1>Сколько стоит веб-сайт?</h1>
        <span>Мы предлагаем три тарифных плана на выбор</span>
      </div>
    </section>
  )
}

export default Header
